// Navigator.tsx

import React, { FC, useState } from 'react';
import SideBar from './SideBar';
import './Navigator.css';

interface NavigatorProps  {
  onClicked: (linkIndex: number) => void;
  LessonIndex: string[];
  TitleText: string;
  lessonId: number;
}

export const Navigator: FC<NavigatorProps > = ({ onClicked, LessonIndex, TitleText, lessonId }) => {
  const [SelectedIndex, setSelectedIndex] = useState<number>(lessonId); // 選択中の項目を管理する状態
  const [isSideBarOpen, setIsSideBarOpen] = useState<boolean>(false); // サイドバーの開閉状態を管理する状態
  
  const handleLinkClick = (linkIndex: number) => {
    // リンクがクリックされたときの処理
    setSelectedIndex(linkIndex);
    setIsSideBarOpen(!isSideBarOpen);
    onClicked(linkIndex);  // 親へ通知
    console.log(`Link clicked in Navigator: ${LessonIndex[linkIndex]}`);
  };

  const handleToggleClick = () => {
    // トグルがクリックされたときの処理
    setIsSideBarOpen(!isSideBarOpen);
    console.log(`Toggle clicked in Navigator:`);
  };

  return (
    <div>

      {/* 半透明のボックス */}
      <div className="overlay-box"></div>

      {/* トグルボタン */}
      <button className="toggle-button" onClick={() => handleToggleClick()} >
          {isSideBarOpen ? '<<' : '>>'}
      </button>
      
      {/* サイドバーコンポーネント */}
      <SideBar onLinkClicked={handleLinkClick} isOpen={isSideBarOpen} LessonIndex={LessonIndex} />

      {/* 授業名 */}
      <span className="Title-text">{TitleText}</span>

      {/* 回 */}
      <div className="Title-Lesson-text">
        {LessonIndex[SelectedIndex]}
      </div>

    </div>
  );
};
