// SideBar.tsx

import React, { FC } from 'react';
import './Navigator.css';

interface SideBarProps {
  onLinkClicked: (linkIndex: number) => void;
  isOpen: boolean;
  LessonIndex: string[];
}

const SideBar: FC<SideBarProps> = ({ onLinkClicked, isOpen, LessonIndex }) => {

  const handleLinkClick = (linkIndex: number) => {
    onLinkClicked(linkIndex);  // 親へ通知
    console.log(`Link clicked in Sidebar: ${LessonIndex[linkIndex]}`);
  };

  return (
    <div className={`sidebar ${isOpen ? 'open' : ''}`}>
      <div className="sidebar-content">
        <ul>
          {LessonIndex.map((lesson, index) => (
            <li key={index}>
              <button onClick={() => handleLinkClick(index)}>{lesson}</button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default SideBar;